<template>
    <div class="alert alert-success" v-if="show">Sabah-ı Şerifleriniz Hayrolsun. Veriler Başarılı Bir Şekilde Kaydedildi. Sisteme Katkı Sağladığınız için Kendinizi Mutlu Hissedin.</div>
    <div class="row">
        <div class="col-md-4">
      <div class="card" id="gonderGelsin">
          <div class="card-header">
              <h5>
                  ÇELİKHANE
              </h5>
          </div>
          <div class="card-body">
                  <div class="form-group row"><label class="col-sm-12 col-form-label" for="kirlibara">Kirli Bara</label>
                      <div class="col-sm-12 pr-0">
                        <input class="form-control" id="name" type="text" name="kbara" required="required" v-model="kbara" /></div>
                  </div>
                  <div class="form-group row"><label class="col-sm-12 col-form-label" for="temizbara">Temiz Bara</label>
                      <div class="col-sm-12 pr-0">
                        <input class="form-control" id="name" type="text" name="tbara" required="required" v-model="tbara" /></div>
                  </div>
                  <div class="form-group row">
                      <div class="col-sm-5 offset-sm-1" id="nesne">
                        <input @click="clkSave" class="btn btn-primary" type="submit" value="Kaydet" /></div>
                  </div>
          </div>
      </div>
  </div>
  <div class="col-md-4">
    <div class="card" id="gonderGelsin2">
      <div class="card-header">
          <h5>
              HADDEHANE
          </h5>
      </div>
      <div class="card-body">
              <div class="form-group row"><label class="col-sm-12 col-form-label" for="hadde">HADDE</label>
                  <div class="col-sm-12 pr-0">
                    <input class="form-control" id="name" type="text" name="hadde" required="required" v-model="hadde" /></div>
              </div>
              <div class="form-group row"><label class="col-sm-12 col-form-label" for="vpsa">VPSA2_3</label>
                  <div class="col-sm-12 pr-0">
                    <input class="form-control" id="password" type="text" name="vpsa" required="required" v-model="vpsa" /></div>
              </div>
              <div class="form-group row">
                  <div class="col-sm-5 offset-sm-1">
                    <input @click="haddeSave" class="btn btn-primary"  type="submit"  value="Kaydet" /></div>
              </div>
      </div>
    </div>
  </div>
  <div class="col-md-4">
      <div class="card" id="gonderGelsin3">
          <div class="card-header">
              <h5>
                  ABA2
              </h5>
          </div>
          <div class="card-body">
                  <div class="form-group row"><label class="col-sm-12 col-form-label" for="aba2">ABA2</label>
                      <div class="col-sm-12 pr-0">
                        <input class="form-control" id="name" type="text" name="aba2" v-model="aba2" /></div>
                  </div>
                  <div class="form-group row">
                      <div class="col-sm-5 offset-sm-1">
                        <input @click="aba2Save" class="aba btn btn-primary" type="submit" value="Kaydet" /></div>
                  </div>
          </div>
      </div>
  </div>
    </div>
  
</template>

<script>
export default {
  name: 'HelloWorld',
  data(){
    return {
        kbara: "",
        tbara: "",
        hadde: "",
        vpsa: "",
        aba2: "",
        show: false
    }
  },
  methods:{
    clkSave(){
        this.$axios.post('/rapor', {
            kbara : this.kbara,
            tbara : this.tbara
        }).then(response => {
            console.log(response)
            this.$store.state.dataFull.kbara = this.kbara;
            this.$store.state.dataFull.tbara = this.tbara;
            this.kbara = "";
            this.tbara = "";
            this.$socket.emit("clk", "update")
            this.show = true;
            setTimeout(() => {
                this.show = false;
            }, 10000);
        });
    },
    haddeSave(){
        this.$axios.post('/rapor', {
            hadde : this.hadde,
            vpsa : this.vpsa
        }).then(response => {
            console.log("response : " + response)
            this.$store.state.dataFull.hadde = this.hadde;
            this.$store.state.dataFull.vpsa = this.vpsa;
            this.hadde = "";
            this.vpsa = "";
            this.$socket.emit("clk", "update")
        });
    },
    aba2Save(){
        this.$axios.post('/rapor', {
            aba2 : this.aba2
        }).then(response => {
            console.log("response : " + response)
            this.$store.state.dataFull.aba2 = this.aba2;
            this.aba2 = "";
            this.$socket.emit("clk", "update")
        });
    }
  },
}
</script>

