<template>
  <AppHeader></AppHeader>
  <router-view></router-view>
  <AppFoother></AppFoother>
</template>

<script>
import AppHeader from './components/shared/AppHeader.vue';
import AppFoother from './components/shared/AppFoother.vue';
// import { mapGetters } from "vuex";
// import { useStore } from "vuex";
// const store = useStore();

export default {
  name: 'App',
  components:{ AppHeader, AppFoother},

  async created(){
     this.$store.dispatch("initApp");
     this.serviceWorker = await navigator.serviceWorker.register("./sw.js");

     this.subscribe();
  },
  mounted(){
    this.$socket.on("clk", ()=>{
      this.$store.dispatch("initApp");
    })
    this.$socket.on("count", data =>{
      this.$store.state.online = data;
      console.log(data)
    })
    this.$socket.on("clientMessage", message =>{
      //this.$store.state.message = message;
      console.log(message)
      alert(message);
    })
  },
    props: ["category"],
  data() {
    return {
      serviceWorker: null,
    };
  },
  methods: {
    async subscribe() {
      this.serviceWorker = await navigator.serviceWorker.ready;
      const clientID = await this.serviceWorker.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey:
          "BJes0VBDf9U-Wu7HZU6Fxtf-W7fR8AOJoORIv-WRqqByx5B0hqPnpuHdiiqEeXm9pKFeMvOHnycU4ipFxdymevg",
      });
      // {...}
      this.$axios
        .post(
          `http://localhost:3010/subscribe`,
          {
            subscriber: clientID,
          }
        )
        .then((category_sub_response) => {
          console.log("category_sub_response :>> ", category_sub_response);
        });
    },
  }
  
}
</script>

