<template>
  <div>
    <div class="row">
      <div class="col-md-12">
          <h5 class="offset-sm-5 mt-5">Girilen ve Alınan Değerler</h5>
          <table class="table table-bordered">
              <thead>
                  <tr>
                      <th>Tarih</th>
                      <th>Kirli Bara</th>
                      <th>Temiz Bara</th>
                      <th>Haddehane</th>
                      <th>VPSA</th>
                      <th>ABA2</th>
                  </tr>
              </thead>
              <tbody>
                <tr>
                    <td>{{ getData.tarih }}</td>
                    <td>{{ kbara }}</td>
                    <td>{{ tbara }}</td>
                    <td>{{ hadde }}</td>
                    <td>{{ vpsa }}</td>
                    <td>{{ aba2 }}</td>
                </tr>
            </tbody>
          </table>
      </div>
  </div>
  <div class="row">
    <div class="col-md-12">
        <h5 class="offset-sm-5 mt-5">RAPOR</h5>
        <table class="table table-bordered">
            <thead>
                <tr>
                    <th>Tarih</th>
                    <th>Kirli Bara</th>
                    <th>Temiz Bara</th>
                    <th>Haddehane</th>
                    <th>VPSA</th>
                    <th>ABA2</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ getData.tarih }}</td>
                    <td>{{ kbara }}</td>
                    <td>{{ sonuc1 }}</td>
                    <td>{{ sonuc2 }}</td>
                    <td>{{ vpsa }}</td>
                    <td>{{ sonuc3 }}</td>
                </tr>
            </tbody>
        </table>
    </div>
  </div>
    <div class="row">
    <div class="col-md-12">
            <br>
            <p>{{ getData.dun }} {{ getData.ayY }} (08:00) - {{ getData.gun }} {{ getData.ayT }} (08:00) arası kirli bara (eaf+lf) tüketim {{getData.kbara}} kwh</p>
            <p>{{ getData.dun }} {{ getData.ayY }} (08:00) - {{ getData.gun }} {{ getData.ayT }} (08:00) arası çelikhane+oksijen temiz bara tüketim {{ sonuc1 }} kwh</p>
            <p>{{ getData.dun }} {{ getData.ayY }} (08:00) - {{ getData.gun }} {{ getData.ayT }} (08:00) arası haddehane tüketim {{ sonuc2 }} kwh</p>
            <p>{{ getData.dun }} {{ getData.ayY }} (08:00) - {{ getData.gun }} {{ getData.ayT }} (08:00) arası ABA-2 tüketim {{ sonuc3 }} kwh</p>
    </div>
  </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed : {
    kbara(){if(this.getData.kbara == null){return "***"}else{return this.getData.kbara;}},
    tbara(){if(this.getData.tbara == null){return "***"}else{return this.getData.tbara;}},
    hadde(){if(this.getData.hadde == null){return "***"}else{return this.getData.hadde;}},
    vpsa(){if(this.getData.vpsa == null){return "***"}else{return this.getData.vpsa;}},
    aba2(){if(this.getData.aba2 == null){return "***"}else{return this.getData.aba2;}},

    sonuc1(){
        if (this.getData.tbara !== null && this.getData.hadde !== null){
            return this.getData.tbara-(this.getData.hadde - this.getData.vpsa);
        }else{
            return "***"
        }
    },
    sonuc2(){
        if (this.getData.hadde !== null){
            return this.getData.hadde - this.getData.vpsa
        }else{
            return "***"
        }
    },
    sonuc3(){
        if (this.getData.aba2 !== null && this.getData.aba2Dun !== null){
            return Math.round((this.getData.aba2 - this.getData.aba2Dun) * 9450)
        }else{
            return "***"
        }
    },
    

    ...mapGetters(["getData"]),
  }
}
</script>

